/* 统计模块样式 */
.statistics {
    padding: 1rem;
}

.stats-tabs {
    display: flex;
    gap: 0.5rem;
    margin-bottom: 1.5rem;
    overflow-x: auto;
    padding-bottom: 0.5rem;
}

.stats-tab {
    background: rgba(255, 255, 255, 0.8);
    border: 2px solid #E8F8F9;
    border-radius: 12px;
    padding: 1rem 1.2rem;
    font-size: 0.9rem;
    color: #2C3E50;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    gap: 0.6rem;
    white-space: nowrap;
    min-width: fit-content;
}

.stats-tab:hover {
    border-color: #CAF4F7;
    background: rgba(202, 244, 247, 0.1);
}

.stats-tab.active {
    background: linear-gradient(135deg, #CAF4F7, #A8E6E8);
    border-color: #7DD3DB;
    color: #2C3E50;
    box-shadow: 0 4px 12px rgba(202, 244, 247, 0.3);
}

.stats-tab i {
    font-size: 1.1rem;
}

.stats-content {
    min-height: 300px;
}

.stats-cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 1rem;
}

.stats-card {
    background: rgba(255, 255, 255, 0.9);
    border-radius: 16px;
    padding: 1.5rem;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
